.cart-dropdown {
  position: absolute;
  width: 240px;
  height: 340px;
  display: flex;
  flex-direction: column;
  padding: 20px;
  border: 1px solid black;
  background-color: white;
  top: 90px;
  right: 40px;
  z-index: 5;
}
.empty-message{
  font-size: 18px;
  margin: 50px auto;
}
.cart-items {
  height: 240px;
  display: flex;
  flex-direction: column;
  overflow: scroll;
}

button {
  margin-top: auto;
}
.cart-button {
  width: 80%;
  height: 40px;
  padding: auto;
  position: absolute;
  top: 280px;
  background-color: black;
  color: white;
  font-family: "Open Sans", sans-serif;
  font-size: large;
  font-weight: 500;
  border: 1px solid black;
}
.cart-button:hover{
  background-color: white;
  color: black;
  cursor: pointer;
}